.logo{
    position:absolute;
    left:0px;
    width: 150px;
    /* border: 1px solid black; */
  }
  
  .nav-links{
    display:flex;
    width: 100%;
    height: 93px;
    list-style-type: none;
    padding: 0;
    margin:0px;
    /* border-top: 1px solid black; */
    /* border-bottom: 1px solid black; */
    background-color:#FF8C00;
  }
  
  .nav-link-li{
    flex:1;
    position:relative;
  }
  
  .nav-link{
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
    text-decoration:none;
    color:black;
  }

  .nav-logo{
      justify-content:start;
  }