.form-for-info {
    background-color: #ff8c00;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: auto;
    padding: 20px 20px 20px 20px;

    border-radius: 5px;

    cursor: default;
}

.form-header {
    padding: 5px 0px 10px 0px;
}

.form-input {
    margin: 5px 0px 5px 0px;
    padding: 3px 3px 3px 3px;
    width: 100%;
    border: none;
    border-radius: 5px;
    outline: 0;
}

.form-button {
    margin: 5px 0px 10px 0px;
    padding: 3px 3px 3px 3px;
    width: 100%;
    border: none;
    border-radius: 5px;
    outline: 0;
    background-color: #8b4513;
    color: white;
}

.form-button:hover {
    background-color: #cd853f;
    color: white;
}

.menu {
    margin-left: 10px;
    width: 100px;
    float: right;
    text-align: right;
    border-radius: 5px;
    background-color: aliceblue;
}

.entity-label {
    float: left;
    text-align: left;
    width: 5cm;
}
.selection-label {
    float: right;
    text-align: right;
    width: auto;
}